/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.course_details{
  white-space: normal !important ;
  overflow: hidden;
  height: 0;
  z-index: 1;
}
.card_outer:hover .course_details_wrapper .course_details,.course_details_wrapper:hover .course_details{
  padding: 2rem;
  height: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.course_details_wrapper::before,.course_details_wrapper::after{
  content: "";
  width: 30px;
  height: 50%;
  z-index: -1;
  position: absolute;
  left: 0;
  background-color: #fff;
}
.course_details_wrapper::before{
  transform: skewX(-20deg);
  top: 0;
}
.course_details_wrapper::after{
  bottom: 0;
  transform: skewX(20deg);
}
.card_outer:last-child .course_details_wrapper::before, .card_outer:last-child .course_details_wrapper::after{
  right: 0;
  left: auto;
}
.card_outer:last-child .course_details_wrapper::before{
  transform: skewX(20deg);
}
.card_outer:last-child .course_details_wrapper::after{
  transform: skewX(-20deg);
}
